px;margin:0 auto;max-width:600px;padding-bottom:30px}div.div-1{grid-column-end:span 2}div.div-2{grid-column-end:span 1}input{font-size:1.3em;width:100%;padding:8px 10px;box-sizing:border-box;border-radius:3px;border:0}textarea{font-size:1.3em;width:100%;padding:8px 10px;box-sizing:border-box;border-radius:3px;border:0;font-family:"Karla","Segoe UI",Tahoma,Geneva,Verdana,sans-serif;height:calc(3lh + 16px)}input[type=date]{font-family:"Karla","Segoe UI",Tahoma,Geneva,Verdana,sans-serif;padding:5px 10px 6px 10px}select{width:100%;font-size:1.3em;padding:8px 10px;border-radius:3px;border:0}input[type=radio]{width:auto;margin:auto 15px;cursor:pointer}.radio-gp label{font-size:1.3em;cursor:pointer}form .btn-primary{display:block;width:100%;margin:10px 0;padding:8px 10px;box-sizing:border-box;text-align:center}div.errorfield{color:tomato}.msg-box{display:grid;grid-template-rows:3lh 7lh auto}