body{font-family:"Markazi",Georgia,"Times New Roman",Times,serif;font-size:16px}h1{color:#f4ce14;font-size:2em;margin:0}h2,p{color:#333;font-family:"Karla","Segoe UI",Tahoma,Geneva,Verdana,sans-serif;margin:10px 0}@media screen and (min-width: 600px){h1{font-size:3em}}header{margin:0 auto;max-width:960px;display:grid;grid-template-columns:30% 70%}main{min-height:calc(100vh - 110px)}nav ul{display:grid;grid-template-columns:auto auto auto;list-style-type:none;margin:0}.header-logo{padding:10px 0 0 10px}.nav-item{font-family:"Karla","Segoe UI",Tahoma,Geneva,Verdana,sans-serif;font-weight:500;font-size:17px;color:#333;cursor:pointer;text-align:center;line-height:60px;color:#333;text-decoration:none}.nav-item:hover{color:#333;background-color:#f4ce14}.row{margin:0 auto;max-width:960px;display:grid;grid-template-columns:60% 40%}.row.div-3{grid-template-columns:32% 32% 32%;column-gap:2%}picture{display:block;overflow:hidden}picture img{width:100%}.menu-title{margin:20px 30px}.home-menu{background-color:#fff;padding:0 15px;margin-bottom:30px}.testimonial-section{background-color:#495e57}.testimonial-section .center-wrapper{max-width:960px;margin:50px auto 0 auto;padding:30px;display:grid;grid-template-columns:auto;row-gap:25px;column-gap:25px}.testimonial-section .center-wrapper .menu-title{margin-top:20px;margin-bottom:20px;color:#edefee}.testimonial-section .center-wrapper h2,.testimonial-section .center-wrapper p{color:#333}.testimonial-section .center-wrapper p{text-align:left}.testimonial-section .center-wrapper .testimonial-item{text-align:center;background-color:#edefee;padding:20px;border-radius:15px}.testimonial-section .center-wrapper .testimonial-item picture{max-width:160px;max-height:160px;border-radius:50%;margin:0 auto}@media screen and (min-width: 600px){.testimonial-section{grid-template-columns:auto auto}.testimonial-section .menu-title{grid-column-start:span 2}}footer{margin:0 auto;padding:10px 30px;font-size:1.2em;background-color:#edefee;color:#333}footer p{margin:10px auto;max-width:960px;color:#495e57}footer .footer_stack{margin:0 auto;max-width:960px;display:grid;grid-template-columns:auto;column-gap:30px}footer .footer_stack section h3{margin-top:20px}footer .footer_stack section ul{list-style-type:"♦ ";padding:0 0 0 20px}footer .footer_stack section ul li{padding:3px 0}footer .footer_stack section a{font-family:"Karla","Segoe UI",Tahoma,Geneva,Verdana,sans-serif;color:#333}footer .footer_stack section.connect .stack_H{margin-top:1em;display:grid;grid-template-columns:auto auto auto auto}footer .footer_stack section.connect img{width:24px}footer .footer_stack .no-wrap{white-space:nowrap}@media screen and (min-width: 500px){footer .footer_stack{grid-template-columns:auto auto}footer section.connect{grid-column-start:span 2}}@media screen and (min-width: 770px){footer .footer_stack{grid-template-columns:auto auto auto}footer section.connect{grid-column-start:span 1}}.about-section{padding:30px}.about-section p{font-size:1.2em}.about-section .row.div-2{grid-template-columns:repeat(1, 1fr);column-gap:20px;row-gap:20px}@media screen and (min-width: 600px){.about-section .row.div-2{grid-template-columns:repeat(2, 1fr);column-gap:20px;row-gap:20px}}.btn-primary{display:inline-block;background-color:#f4ce14;color:#333;border:0;font-size:16pt;font-family:"Karla","Segoe UI",Tahoma,Geneva,Verdana,sans-serif;font-weight:500;padding:8px 25px;border-radius:20px;text-decoration:none;cursor:pointer}.btn-primary:hover{background-color:#fbdabb}.btn-primary:disabled{background-color:#999}